<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container-fluid">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Billing Templates</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-md-auto col-12 ">
                            <template v-if="$can.view('/billing_templates/add')">
                                <q-btn       :rounded="false"  size=""  color="primary" no-caps  unelevated   :to="`/billing_templates/add`" class="full-width">
                                    <q-icon name="add"></q-icon>                                
                                    Add New Billing Templates 
                                </q-btn>
                            </template>
                        </div>
                        <div class="col-md-auto col-12 ">
                            <q-input debounce="1000" outlined dense  placeholder="Search" v-model="searchText">
                            <template v-slot:append>
                                <q-icon name="search"></q-icon>
                            </template>
                            </q-input>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container-fluid">
                <div class="row q-col-gutter-md">
                    <div class="col comp-grid">
                        <div>
                            <q-chip v-if="searchText" icon="search" removable @remove="searchText='';$route.query.search=''">
                            Search: <strong class="q-ml-sm"> {{ searchText }} </strong>
                            </q-chip>
                        </div>
                        <div class="">
                            <div>
                                <template v-if="showBreadcrumbs">
                                    <q-breadcrumbs class="q-pa-md" v-if="searchText || fieldName">
                                        <template v-if="fieldName">
                                            <q-breadcrumbs-el v-if="$route.query.tag" icon="arrow_back" class="text-capitalize" :label="$route.query.tag" to="/billing_templates"></q-breadcrumbs-el>
                                            <q-breadcrumbs-el v-else icon="arrow_back" class="text-capitalize" :label="fieldName|readable" to="/billing_templates"></q-breadcrumbs-el>
                                            <q-breadcrumbs-el v-if="$route.query.label" :label="$route.query.label"></q-breadcrumbs-el>
                                            <q-breadcrumbs-el v-else :label="fieldValue"></q-breadcrumbs-el>
                                        </template>
                                    </q-breadcrumbs>
                                </template>
                                <div class="relative-position">
                                    <!-- page records template -->
                                    <template >
                                        <q-table 
                                        :grid="$q.screen.lt.md"
                                        :flat="$q.screen.gt.md"
                                        table-header-class="text-h4 "
                                        :bordered="false"
                                        :columns="$menus.Amateur_Doc_TypesTableHeaderItems" 
                                        :data="records"
                                        binary-state-sort
                                        separator="horizontal"
                                        :dense="true"
                                        :selected.sync="selectedItems"
                                        selection="multiple"
                                        row-key="id" 
                                        :pagination.sync="pagination"
                                        hide-bottom
                                        @request="setPagination"
                                        :loading="loading">
                                        <!-- Start of Table Layout -->
                                        <template v-slot:body="props">
                                            <q-tr :props="props">
                                                <q-td auto-width>
                                                    <q-checkbox v-model="props.selected"></q-checkbox>
                                                </q-td>
                                                <q-td  key="id" :props="props">
                                                    <q-btn padding="xs"   :rounded="false"  color="primary"  no-caps  unelevated   flat :to="'/billing_templates/view/' + props.row.id">{{ props.row.id }}</q-btn>
                                                </q-td>
                                                <q-td  key="created_at" :props="props">
                                                    {{ props.row.created_at }}
                                                </q-td>
                                                <q-td  key="updated_at" :props="props">
                                                    {{ props.row.updated_at }}
                                                </q-td>
                                                <q-td  key="name" :props="props">
                                                    {{ props.row.name }}
                                                </q-td>
                                                <q-td  key="description" :props="props">
                                                    {{ props.row.description }}
                                                </q-td>
                                                <q-td key="btnactions" :props="props">
                                                    <div class="row q-col-gutter-xs justify-end">
                                                        <q-btn icon="menu" padding="xs" round flat color="grey">
                                                            <q-menu transition-show="flip-right"  transition-hide="flip-left" self="center middle" anchor="center middle">
                                                                <q-list dense rounded nav>
                                                                    <template v-if="$can.view('billing_templates/view')">
                                                                        <q-item link clickable v-ripple :to="`/billing_templates/view/${props.row.id}`">
                                                                            <q-item-section>
                                                                                <q-icon color="primary"  size="sm" name="visibility"></q-icon>
                                                                            </q-item-section>
                                                                            <q-item-section>View</q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    <template v-if="$can.view('billing_templates/edit')">
                                                                        <q-item link clickable v-ripple :to="`/billing_templates/edit/${props.row.id}`">
                                                                            <q-item-section>
                                                                                <q-icon color="positive"  size="sm" name="edit"></q-icon>
                                                                            </q-item-section>
                                                                            <q-item-section>Edit</q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    <template v-if="$can.view('billing_templates/delete')">
                                                                        <q-item link clickable v-ripple @click="deleteItem(props.row.id)">
                                                                            <q-item-section>
                                                                                <q-icon color="negative"  size="sm" name="clear"></q-icon>
                                                                            </q-item-section>
                                                                            <q-item-section>Delete</q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                </q-list>
                                                            </q-menu>
                                                        </q-btn>
                                                    </div>
                                                </q-td>
                                            </q-tr>
                                        </template>
                                        <!-- End of Table Layout-->
                                        <!-- Start of Mobile Grid Layout-->
                                        <template v-slot:item="props">
                                            <div class="q-pa-xs col-xs-12 col-sm-6 col-md-4 col-lg-3 grid-style-transition" :style="props.selected ? 'transform: scale(0.95);' : ''">
                                                <q-card   class="nice-shadow-18">
                                                    <q-card-section>
                                                        <div class="row q-py-sm q-col-gutter-md justify-content-between">
                                                            <div class="col-auto text-caption">
                                                                Id
                                                            </div>
                                                            <div class="col text-right">
                                                                <q-btn padding="xs"   :rounded="false"  color="primary"  no-caps  unelevated   flat :to="'/billing_templates/view/' + props.row.id">{{ props.row.id }}</q-btn>
                                                            </div>
                                                        </div>
                                                        <div class="row q-py-sm q-col-gutter-md justify-content-between">
                                                            <div class="col-auto text-caption">
                                                                Created At
                                                            </div>
                                                            <div class="col text-right">
                                                                {{ props.row.created_at }}
                                                            </div>
                                                        </div>
                                                        <div class="row q-py-sm q-col-gutter-md justify-content-between">
                                                            <div class="col-auto text-caption">
                                                                Updated At
                                                            </div>
                                                            <div class="col text-right">
                                                                {{ props.row.updated_at }}
                                                            </div>
                                                        </div>
                                                        <div class="row q-py-sm q-col-gutter-md justify-content-between">
                                                            <div class="col-auto text-caption">
                                                                Name
                                                            </div>
                                                            <div class="col text-right">
                                                                {{ props.row.name }}
                                                            </div>
                                                        </div>
                                                        <div class="row q-py-sm q-col-gutter-md justify-content-between">
                                                            <div class="col-auto text-caption">
                                                                Description
                                                            </div>
                                                            <div class="col text-right">
                                                                {{ props.row.description }}
                                                            </div>
                                                        </div>
                                                    </q-card-section>
                                                    <q-separator></q-separator>
                                                    <div class="row justify-between">
                                                        <div><q-checkbox v-model="props.selected"></q-checkbox></div>
                                                        <q-card-actions>
                                                            <div class="row q-col-gutter-xs justify-end">
                                                                <q-btn icon="menu" padding="xs" round flat color="grey">
                                                                    <q-menu transition-show="flip-right"  transition-hide="flip-left" self="center middle" anchor="center middle">
                                                                        <q-list dense rounded nav>
                                                                            <template v-if="$can.view('billing_templates/view')">
                                                                                <q-item link clickable v-ripple :to="`/billing_templates/view/${props.row.id}`">
                                                                                    <q-item-section>
                                                                                        <q-icon color="primary"  size="sm" name="visibility"></q-icon>
                                                                                    </q-item-section>
                                                                                    <q-item-section>View</q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                            <template v-if="$can.view('billing_templates/edit')">
                                                                                <q-item link clickable v-ripple :to="`/billing_templates/edit/${props.row.id}`">
                                                                                    <q-item-section>
                                                                                        <q-icon color="positive"  size="sm" name="edit"></q-icon>
                                                                                    </q-item-section>
                                                                                    <q-item-section>Edit</q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                            <template v-if="$can.view('billing_templates/delete')">
                                                                                <q-item link clickable v-ripple @click="deleteItem(props.row.id)">
                                                                                    <q-item-section>
                                                                                        <q-icon color="negative"  size="sm" name="clear"></q-icon>
                                                                                    </q-item-section>
                                                                                    <q-item-section>Delete</q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                        </q-list>
                                                                    </q-menu>
                                                                </q-btn>
                                                            </div>
                                                        </q-card-actions>
                                                    </div>
                                                </q-card>
                                            </div>
                                        </template>
                                        <!-- End of Mobile Grid Layout-->
                                        </q-table>
                                    </template>
                                    <!-- page loading indicator template -->
                                    <template v-if="loading">
                                        <q-inner-loading :showing="loading">
                                            <q-spinner color="primary" size="30px"> 
                                            </q-spinner>
                                        </q-inner-loading>
                                    </template>
                                    <!-- page empty record template -->
                                    <template v-if="!loading && !records.length">
                                        <q-card :flat="$q.screen.gt.md">
                                            <q-card-section>
                                                <div class="text-grey text-h6 text-center">
                                                    No record found
                                                </div>
                                            </q-card-section>
                                        </q-card>
                                    </template>
                                    <!-- page footer template-->
                                    <template v-if="showFooter">
                                        <div class="">
                                            <div class="q-pa-sm q-my-md" v-show="!loading">
                                                <div class="row justify-between">
                                                    <div class="row q-col-gutter-md">
                                                        <template v-if="$can.view('billing_templates/delete')">
                                                            <div>
                                                                <q-btn display-style="modal"    :rounded="false"  no-caps  unelevated   color="negative" padding="xs" @click="deleteItem(selectedItems)" v-if="selectedItems.length" icon="delete_sweep" class="q-my-xs" title="Delete Selected"></q-btn>
                                                            </div>
                                                        </template>
                                                    </div>
                                                    <div v-if="paginate && totalRecords > 0" class="row q-col-gutter-md justify-center">
                                                        <div class="col-auto">
                                                            <q-chip>Records {{recordsPosition}} of {{totalRecords}}</q-chip>
                                                        </div>
                                                        <div>
                                                            <q-pagination  color="primary" flat glossy  input v-model="pagination.page" :direction-links="true" :boundary-links="true" :max-pages="5" :boundary-numbers="true" :max="totalPages"></q-pagination>
                                                        </div>
                                                    </div>
                                                </div>  
                                            </div>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
	import { PageMixin } from "../../mixins/page.js";
	import { ListPageMixin } from "../../mixins/listpage.js";
	import { mapActions, mapGetters, mapState } from "vuex";
	export default {
		name: 'listBillingtemplatesPage',
		components: {
        },
		mixins: [PageMixin, ListPageMixin ],
		props: {
			primaryKey : {
				type : String,
				default : 'id',
			},
			pageName : {
				type : String,
				default : 'billing_templates',
			},
			routeName : {
				type : String,
				default : 'billing_templateslist',
			},
			apiPath : {
				type : String,
				default : 'billing_templates/index',
			},
			multiCheckbox: {
				type: Boolean,
				default: true,
			},
			msgBeforeDelete: {
				type: String,
				default: "Are you sure you want to delete this record?",
			},
		},
		data() {
            return {
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					//set browser page title
					return "Billing Templates"
				}
			},
			records: {
				get: function () {
					return this.$store.getters["billing_templates/records"];
				},
				set: function (value) {
					this.$store.commit("billing_templates/setRecords", value);
				},
			},
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		watch: {
			apiUrl: function () {
				this.load();
			},
			$route (to, from){
				//only fetch data when navigated to this page
				if(to.name == this.routeName){
					this.load();
				}
			},
		},
		methods: {
			...mapActions("billing_templates", ["fetchRecords", "deleteRecord"]),
			load: function() {
				if (!this.loading) {
					this.loading = true;
					let url = this.apiUrl;
					let payload = {
						url,
						merge: false
					}
					this.fetchRecords(payload).then(
						(response) => {
							this.loading = false;
							this.ready = true;
							this.totalRecords = response.total_records;
							this.recordCount = response.record_count;
							this.pagination.rowsNumber = this.totalRecords;
							window.scrollTo(0, 0);
						},
						(response) => {
							this.loading = false;
							this.showPageRequestError(response);
						}
					);
				}
			},	
		},
	};
</script>
<style scoped>
</style>
